import $ from 'jquery'
import './index.css'
import { fetchSocket } from '@/util/request'
import headerHbs from './index.hbs'

export default class Header {  
  constructor() {
    // 初始化 DOM 节点
    // ==============
    $('#header').append(headerHbs())
  }

  /**
   * 绑定事件
   * @return {void} void
   */
  bindEvent() {
    $('.header-tab-item').on('click', e => {
      let that = $(e.currentTarget)
      let type = e.currentTarget.dataset.type

      // 切换样式
      // =======
      $('.header-tab-item').attr('class', 'header-tab-item')
      that.attr('class', 'header-tab-item emphasis')

      // 点击触发的事件
      // ============
      switch(type) {
      case 'zhts':
        console.log('综合态势')
        break
      case 'rlts':
        console.log('人流态势')
        break
      case 'clts':
        console.log('车流态势')
        break
      default:
        console.log('work')
      }
    }) 
  }

  /**
   * 执行
   * @return {void} void
   */
  render() {
    this.bindEvent()
    $('.header-tab-item').eq(0).click()
  }
}



